import { CreditCardOutlined, ShoppingCartOutlined } from "@ant-design/icons";
import { Button, Col, Divider, InputNumber, Row } from "antd";
import React, { useState } from "react";
import Cart from "../Cart";
import Discount from "../Discount";
import "./style.scss";

export default function Product() {
  const [openCart, setOpenCart] = useState(false);
  const [data, setData] = useState([]);
  const [quantity, setQuantity] = useState(1);
  const [priceSale, setPriceSale] = useState(19.99);
  const [price, setPrice] = useState(33.99);
  const [option, setOption] = useState("BUY 1");
  const [id, setId] = useState(1);

  const addToCart = (product, quantity) => {
    const productInCart = data.find((item) => item.id === product.id);

    if (productInCart) {
      const updatedCart = data.map((item) =>
        item.id === product.id
          ? { ...item, quantity: item.quantity + quantity }
          : item
      );
      setData(updatedCart);
    } else {
      setData([...data, { ...product, quantity }]);
    }
  };

  function removeItem(id) {
    const newData = data.filter((item) => id !== item.id);
    setData(newData);
  }

  var newItem = {
    id: id,
    title: "Latex Middle Finger Headgear",
    priceSale: priceSale,
    price: price,
    option: option,
    // quantity: value,
  };

  const options = [
    {
      id: 1,
      option: "BUY 1",
      price: 33.99,
      priceSale: 19.99,
    },
    {
      id: 2,
      option: "BUY 2 GET 10% OFF",
      price: 59.99,
      priceSale: 35.99,
    },
    {
      id: 3,
      option: " BUY 4 GET 15% OFF & FREE SHIPPING",
      price: 113.99,
      priceSale: 67.97,
    },
  ];

  const handleIncrease = () => {
    setQuantity(quantity + 1);
  };

  const handleDecrease = () => {
    if (quantity > 1) {
      setQuantity(quantity - 1);
    }
  };
  return (
    <div className="product-wrapper">
      <div className="product-form">
        <div className="product__title">Latex Middle Finger Headgear</div>
        <div className="product__price">
          <del
            className="lbl-compared-price"
            style={{ display: "inline-block" }}
          >
            ${price}
          </del>
          <span className="lbl-price">${priceSale}</span>
        </div>
        <div className="countdownTimer">
          Hurry up. This deal will end soon!!
        </div>

        <div className="product__options">
          <div className="product__varians">
            <div>
              <label className="label">BUY MORE SAVE MORE</label>
            </div>
            <div className="product__body">
              <div className="product__body--field">
                {options.map((item, index) => {
                  return (
                    <div key={index}>
                      <div className="control">
                        <Button
                          onClick={() => {
                            setPriceSale(item.priceSale);
                            setPrice(item.price);
                            setOption(item.option);
                            setId(item.id);
                          }}
                          className="button"
                        >
                          {item.option}
                        </Button>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </div>

        <Row
          gutter={8}
          justify="space-between"
          style={{ margin: 0, columnGap: "normal", rowGap: 15 }}
        >
          <Col className="inputNumber" span={8}>
            <div className="field">
              <p className="control">
                <Button onClick={handleDecrease} className="icon-button left">
                  -
                </Button>
              </p>
              <p className="control control-input">
                <InputNumber
                  className="icon-button center"
                  value={quantity}
                  min={1}
                  readOnly
                />
              </p>
              <p className="control">
                <Button onClick={handleIncrease} className="icon-button right">
                  +
                </Button>
              </p>
            </div>
          </Col>
          <Col className="btn-group" span={8}>
            <Button
              onClick={() => {
                addToCart(newItem, quantity);
                setOpenCart(true);
              }}
              className="add-to-cart-button"
            >
              <ShoppingCartOutlined style={{ fontSize: 24 }} />
              ADD TO CART
            </Button>
          </Col>
          <Col className="btn-group" span={8}>
            <Button
              style={{ background: "#366AE2" }}
              className="add-to-cart-button buy"
            >
              <CreditCardOutlined style={{ fontSize: 24 }} />
              BUY IT NOW
            </Button>
          </Col>
        </Row>
        <Button
          style={{
            background: "#ffc439",
            width: "100%",
            padding: "27px",
            margin: "15px 0",
          }}
          className="paypal-button"
        >
          <div className="paypal">
            <img
              src="data:image/svg+xml;base64,"
              alt=""
              className="paypal-logo"
            />
          </div>
        </Button>
        <Divider />
        <div className="discount">
          <div className="discount__title">Buy More Save More</div>
          <Discount
            title="2 items get 10% off"
            subTitle="on each product"
            onClick={() => {
              // addToCart(newItem, 2);
              setOpenCart(true);
            }}
          />
          <Discount
            title="4 items get 15% off"
            subTitle="on each product"
            onClick={() => {
              setPriceSale(67.97);
              setPrice(113.99);
              setOption(" BUY 4 GET 15% OFF & FREE SHIPPING ");
              setId(3);
              addToCart(newItem, 4);
              setOpenCart(true);
            }}
          />
        </div>
        <Divider style={{ margin: "65px 0" }} />

        <Cart
          open={openCart}
          close={() => {
            setOpenCart(false);
          }}
          id={id}
          data={data}
          remove={removeItem}
          // value={value}
          // setValue={setValue}
          priceSale={priceSale}
          setData={setData}
          quantity={quantity}
        />
      </div>
    </div>
  );
}
